body {
  background-image: url("./assets/images/day-bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  min-height: 100vh;
}

a,
p,
span,
h1,
h2,
h3,
h4,
h5 {
  color: #fff;
}

.footer-copyright {
  font-size: 0.8rem;
}

.learn-more-link:hover {
  color: #fff;
  text-decoration: none;
}

.About {
  text-align: center;
  min-height: calc(100vh - 51px);
}

.ContactUs {
  text-align: center;
  height: calc(100vh - 51px);
}

.company-name {
  text-transform: uppercase;
}

.contact-address,
.contact-details {
  font-size: 1.2rem;
}

.btn-lg {
  border-radius: 0;
}

.home-btn {
  width: 150px;
}

.Home {
  height: 100vh;
  /* background-image: url("./assets/images/main-bg.jpg"); */
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  /* box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2); */
}

.contact-row {
  height: 100%;
  align-items: center;
}

.home-row {
  height: 92%;
  align-items: center;
}

.navbar {
  position: absolute;
  width: 100%;
  background: transparent !important;
}

.navbar-logo {
  height: 50px;
}

.map-div {
  height: 256px;
  width: 100%;
}

.about-desc {
  background: rgba(0, 0, 0, 0.5);
  padding: 1rem;
  margin-top: 2rem;
}

.contact-desc {
  background: rgba(0, 0, 0, 0.5);
  padding: 1rem;
}

.animated-title {
  color: #222;
  font-family: Roboto, Arial, sans-serif;
  height: 90vmin;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 90vmin;
}

.animated-title > div {
  height: 50%;
  overflow: hidden;
  position: absolute;
  width: 100%;
}

.animated-title > div div {
  font-size: 12vmin;
  padding: 2vmin 0;
  position: absolute;
}

.animated-title > div div .intro-text {
  display: block;
}

.animated-title > div.text-top {
  border-bottom: 1vmin solid #000;
  top: 0;
}

.animated-title > div.text-top div {
  animation: showTopText 1s;
  animation-delay: 0.5s;
  animation-fill-mode: forwards;
  bottom: 0;
  transform: translate(0, 100%);
}

/* .animated-title > div.text-top div .intro-text:first-child {
  color: #767676;
} */

.animated-title > div.text-bottom {
  bottom: 0;
}

.animated-title > div.text-bottom div {
  animation: showBottomText 0.5s;
  animation-delay: 1.75s;
  animation-fill-mode: forwards;
  top: 0;
  transform: translate(0, -100%);
}

@keyframes showTopText {
  0% {
    transform: translate3d(0, 100%, 0);
  }

  40%,
  60% {
    transform: translate3d(0, 50%, 0);
  }

  100% {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes showBottomText {
  0% {
    transform: translate3d(0, -100%, 0);
  }

  100% {
    transform: translate3d(0, 0, 0);
  }
}

@media (max-width: 1024px) {
  .map-div {
    height: 284px;
  }
}

@media (max-width: 768px) {
  .map-div {
    height: 322px;
  }

  .navbar-collapse {
    padding: 1rem;
    background: rgba(0, 0, 0, 1);
  }
}

@media (max-width: 425px) {
  h1 {
    font-size: 2rem;
  }

  .intro-text:first-child {
    padding-bottom: 1.6rem;
  }

  .ContactUs {
    height: 100%;
  }

  .contact-desc {
    margin-top: 2rem;
  }
}

@media (max-width: 375px) {
  h1 {
    font-size: 1.9rem;
  }

  .intro-text:first-child {
    padding-bottom: 0;
  }
}

@media (max-width: 320px) {
  h1 {
    font-size: 1.6rem;
  }

  .navbar-logo {
    height: 40px;
  }
}
